.professionals {
  display: grid;
  grid-template: 1fr / 1fr minmax(auto, 1400px) 1fr;
}

.professionals__block {
  grid-column: 2/3;
  text-align: center;
}

.professionals__title {
  display: grid;
  text-align: center;
  padding: 18px;
  margin: 20px 0px;
  font-size: 25px;
  font-weight: 800;
}

.professionals__title_border {
  border-top: 1px solid rgb(150, 150, 150, 0.2);
  border-bottom: 1px solid rgb(150, 150, 150, 0.2);
}

.professionals__professional{
  display: grid;
  grid-template: 1fr / minmax(auto, 450px) minmax(auto, 450px) minmax(auto, 450px);
}

.professionals__pagination_wight {
  text-align: center;
  font-size: 27px;
  font-weight: 900;
}

.professional__photos img{
  border-radius: 50%;
  max-width: 300px;
}